			var txt = '<table cellspacing="1" cellpadding="2" border="0"><tr>';
			for (var i = 0, c = toollBtns.length; i < c; i++) {
				txt += '<td><a href="#" onclick="' + toollBtns[i][1] + ' return false;">' + toollBtns[i][0] + '</a></td>';
			}
			txt += '</tr></table>';
			node.innerHTML = txt;
